.title__container-confetti {
  position: relative;
  height: 100vh;
  font-size: 1rem;
}
.party__title {
  padding-top: 8%;
  line-height: 1.5em;
  font-size: 2.5rem;
  z-index: 100;
}

.party {
  z-index: 100;
  font-size: 1.5rem;
}

.bottom {
  position: absolute;
  font-size: 2rem !important;
  left: 350px;
  bottom: -50px;
  /* margin-bottom: 10px; */
  cursor: pointer;
}

#party-info {
  position: absolute;
  background: rgb(250, 250, 250);
  box-shadow: 10px 10px 10px rgba(238, 226, 238, 0.6);
  padding: 20px;
  margin: 0 auto;
  height: 800px;
  left: 10%;
  right: 0;
  text-align: center;
  bottom: 15%;
  width: 750px;
  z-index: 2;
  color: dimgray;
}

.confettie-wrap {
  padding: 0px;
  position: relative;
  height: 100vh;
  width: 100vw;
}

a {
  color: inherit;
  text-decoration: none;
}

.title a {
  color: #0070f3;
  text-decoration: none;
}

.title a:hover,
.title a:focus,
.title a:active {
  text-decoration: underline;
}

.title {
  margin: 0;
  line-height: 1.15;
  font-size: 4rem;
}

.title,
.description {
  text-align: center;
}

.description {
  line-height: 1.5;
  font-size: 1.5rem;
}

code {
  background: #fafafa;
  border-radius: 5px;
  padding: 0.75rem;
  font-size: 1.1rem;
  font-family: Menlo, Monaco, Lucida Console, Liberation Mono,
    DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace;
}

.grid {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  max-width: 800px;
  margin-top: 3rem;
}

.card {
  margin: 1rem;
  flex-basis: 45%;
  padding: 1.5rem;
  text-align: left;
  color: inherit;
  text-decoration: none;
  border: 1px solid #eaeaea;
  border-radius: 10px;
  transition: color 0.15s ease, border-color 0.15s ease;
}

.card:hover,
.card:focus,
.card:active {
  color: #0070f3;
  border-color: #0070f3;
}

.card h3 {
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
}

.card p {
  margin: 0;
  font-size: 1.25rem;
  line-height: 1.5;
}

.logo {
  height: 1em;
}

* {
  box-sizing: border-box;
}

.sheep {
  position: fixed;
  top: 100px;
  left: 100px;
  width: 320px;
  height: 300px;
  border-radius: 5%;
  border: #fff 3px solid;
  z-index: 100;
  background-image: url(/static/media/sheep.5a0daa3e.jpg);
  background-color: transparent;
  display: block;
  cursor: pointer;
}
.sheep.blurHighlight-sheep {
  animation: blurHighlight-sheep 0.75s ease 3;
}
@keyframes blurHighlight-sheep {
  from {
    box-shadow: 0px 0px 10px #f5eddc, 0px 0px 100px #f5eddc,
      0px 0px 150px #f5eddc, 0px 0px 25px #f5eddc, 0px 0px 25px #f5eddc,
      0px 0px 25px #f5eddc, 0px 0px 25px #f5eddc, 0px 0px 25px #f5eddc,
      0px 0px 50px #f5eddc, 0px 0px 50px #f5eddc, 0px 0px 50px #e6f704,
      0px 0px 550px #e6f704, 0px 10px 100px #e6f704,
      0px 10px 500px #f5eddc, 0px 10px 100px #f5eddc,
      0px 10px 100px #f5eddc, 0px -10px 100px #f5eddc,
      0px -10px 100px #f5eddc;
  }
}
