  margin: 0;
}

.title__container {
  font-size: 10rem;
  text-align: center;
  color: chocolate;
  margin: 150px;
  padding: 150px 20px 60px 70px;
  height: 100vh;
}



.tag {
  font-size: 10rem;
  margin-top: 10px;
  font-size: 10rem;
  text-shadow: 0px 0px 10px #f5eddc;
  color: #fff;
  padding: 10px 20px;
  margin: 0 25px 10px 0;
  display: inline-block;
}

.tag.highlight {
  color: #fff;
  font-size: 10rem;
  font-weight: 1000;
  animation: blurHighlight 0.5s ease-out 10;
}
.tag-top.highlight {
  padding-top: 50px;
  color: #fff;
  font-size: 6rem;
  font-weight: 800;
  animation: blurHighlight 0.75s ease-out 2;
}
@keyframes blurHighlight {
  from {
    text-shadow: 0px 0px 10px #f5eddc, 0px 0px 100px #f5eddc,
      /* 0px 0px 250px #f5eddc, 0px 0px 25px #f5eddc, 0px 0px 25px #f5eddc, */
0px 0px 25px #f5eddc,
      0px 0px 25px #f5eddc, 0px 0px 25px #f5eddc,
      /* 0px 0px 50px #f5eddc, 0px 0px 50px #f5eddc, 0px 0px 50px #e6f704, */
0px 0px 550px #e6f704,
      0px 10px 50px #e6f704, 0px 10px 50px #f5eddc;
    /* 0px 10px 100px #f5eddc, 0px 10px 50px #f5eddc, 0px -10px 100px #f5eddc, */
    /* 0px -10px 100px #f5eddc; */
  }
}


body {
  background-color: rgb(24, 23, 23);
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;
}
* {
  box-sizing: border-box;
}

